.recoverPageContent {
  width: 100%;
  height: 100%;
  background: rgb(223, 246, 242);
  background: linear-gradient(
    0deg,
    rgba(223, 246, 242, 1) 0%,
    rgba(227, 245, 241, 1) 11%,
    rgba(255, 255, 255, 1) 100%
  );
  display: flex;
  flex-direction: column;
  align-items: center;
}

.recoverTitleContainer {
  margin-top: 50px;
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.recoverTitleContainer img {
  width: 80px;
  margin-right: 10px;
}

.recoverTitle {
  font-family: Poppins-SemiBold;
  font-size: 26px;
  color: #266b63;
  text-shadow: 1px 1px 1.5px #000000;
}

.recoverText {
  margin-top: 50px;
  text-align: center;
  padding-left: 20px;
  padding-right: 20px;
  max-width: 500px;
  font-family: Poppins-SemiBold;
  font-size: 15px;
  color: #000000;
}

.recoverOptionsContainer_form {
  width: 100%;
  min-height: 170px;
  margin-bottom: 50px;
}

.recoverOptionsContainer {
  width: 100%;
  height: 170px;
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.recoverInputsContainer {
  width: 100%;
  height: 230px;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.recoverOption {
  width: 85%;
  max-width: 500px;
  height: 42px;
  border-radius: 50px;
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Poppins-Bold;
  font-size: 14px;
  color: rgb(39, 39, 39);
  cursor: pointer;
  text-align: center;
}

.recover-row {
  display: flex;
  justify-content: center;
  align-items: center;
}

.inputFieldrecover {
  font-family: Poppins-Bold;
}

.recoverOption:hover {
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.25);
}

.recoverOption--google {
  border-color: rgb(9, 141, 141);
  background-color: white;
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.1);
}

.recoverOption--input {
  border-color: rgb(9, 141, 141);
  /* background-color: rgba(117, 117, 117, 0.048); */
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  padding: 10px;
  margin-bottom: 10px;
}
.recoverError {
  margin-top: 20px;
  margin-bottom: -30px;
  text-align: center;
  padding: 10px;
  width: 100%;
  max-width: 400px;
  background-color: rgb(252, 147, 147);
  font-weight: bold;
  border-radius: 10px;
  display: none;
}
.recoverOption--submit {
  border-color: rgb(9, 141, 141);
  background-color: #1878f1;
  text-shadow: 1px 1px 1.5px #00000028;
  color: white;
  width: 50%;
  box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  padding: 10px;
  margin-bottom: -5px;
  margin-top: 8px;
  --background: #1878f1;
  --box-shadow: unset;
  --background-hover: none;
}

.socialIcon {
  width: 22px;
  margin-right: 10px;
  fill: white;
}

.separator {
  width: 50px;
}

.recoverOption--facebook {
  border: none;
  background-color: #1878f1;
  color: white;
  text-shadow: 1px 1px 1.5px #00000028;
}

.recoverOption--local {
  height: 30px;
  border: none;
  color: #1c443f;
  text-shadow: 1px 1px 1.5px #00000028;
}

.recoverOption--local:hover {
  box-shadow: none;
  text-shadow: 2px 2px 2.5px #00000028;
  text-decoration: underline;
}

.recoverOption--create {
  white-space: nowrap;
  margin-top: 10px;
  margin-bottom: -10px;
}

.recoverImg {
  width: 65%;
  max-width: 290px;
}

.prhaserecover {
  text-align: center;
  font-family: Poppins-ExtraBold;
  color: rgb(32, 32, 32);
  width: 80%;
  font-size: 16px;
  margin-bottom: -20px;
}

.backBtn {
  color: #1c443f;
  text-shadow: 1px 1px 1.5px #00000028;
  cursor: pointer;
  padding: 10px;
  border-radius: 50%;
}

.backBtn:hover {
  text-shadow: 2px 2px 2px #00000028;
  background-color: rgba(125, 202, 179, 0.281);
}
