.Content {
  /* background-image: url("https://e00-marca.uecdn.es/assets/multimedia/imagenes/2016/06/09/14655007817465.jpg"); */
  height: 90%;
  width: 100%;
  background-size: cover;
  display: flex;
  flex-direction: column;
  /* justify-content: flex-end; */
}

.ProfileImage {
  border-radius: 100%;
  height: 200px;
  width: 200px;
  object-fit: cover;
  background-color: transparent;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.Content__info {
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  /* height: 60%; */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Content__ProfileImage {
  height: 7rem;
  width: 7rem;
  /* margin-top: -5rem; */
  border-radius: 1000px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  margin-bottom: 0.5rem;
}

.Content__Buttons {
  width: 100%;
  max-width: 600px;
}